@tailwind base;
@tailwind components;
@tailwind utilities;
.fullscreen:hover .fs-icon .arrow {
  fill: transparent;
}
.passwrapper {
  margin: auto;
  width: 100%;
  height: 100vh;
  max-height: 100%;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgb(0 0 0 / 15%);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  border-radius: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 10px;
  align-items: center;
}
.passwrapper .topbar {
  height: 4rem;
}
.passwrapper .bulgy-checkboxs-label {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  letter-spacing: 1px;
  font-family: Julius Sans One;
  display: flex;
  gap: 10px;
  align-items: center;
}
.passwrapper .bulgy-checkboxs {
}
.passwrapper .bulgy-checkboxs-label:hover input:not(:checked) ~ .checkbox {
  opacity: 0.8;
}
.passwrapper .bulgy-checkboxs .label {
  display: flex;
  align-items: center;
}
.passwrapper .bulgy-checkboxs .label span {
  line-height: 1em;
}
.passwrapper .bulgy-checkboxs input {
  position: absolute;
  cursor: pointer;
  height: 0;
  width: 0;
  left: -2000px;
}
.passwrapper .bulgy-checkboxs input:checked ~ .checkbox {
  transition: all 0.3s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .checkbox:after {
  opacity: 1;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label {
  font-size: 18px;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span {
  animation: bulge 0.5s forwards;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(1) {
  animation-delay: 0.025s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(2) {
  animation-delay: 0.05s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(3) {
  animation-delay: 0.075s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(4) {
  animation-delay: 0.1s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(5) {
  animation-delay: 0.125s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(6) {
