}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(8) {
  animation-delay: 0.2s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(9) {
  animation-delay: 0.225s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(10) {
  animation-delay: 0.25s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(11) {
  animation-delay: 0.275s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(12) {
  animation-delay: 0.3s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(13) {
  animation-delay: 0.325s;
}
.passwrapper .bulgy-checkboxs input:checked ~ .label span:nth-child(14) {
