}
.passwrapper .checkbox {
  height: 30px;
  width: 30px;
  transform: scale(1);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  color: var(--text-color);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgb(0 0 0 / 15%);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
}
.passwrapper .checkbox:hover {
  transform: scale(1.1);
}
.passwrapper .checkbox:active {
  transform: scale(0.9);
}
.passwrapper .w3-rowcheckboxs {
  text-align: center;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 16px;
  gap: 15px;
  align-items: flex-start;
}
.passwrapper label input:checked + .checkbox:after {
  transform: scale(1);
}
.passwrapper label .checkbox:after {
  opacity: 0;
  border-radius: 50%;
  transform: scale(0);
  content: "︎✔";
  font-size: 20px;
  transition: 0.1s;
  -webkit-text-stroke: 1px #2c0101;
}
.passwrapper .decrementincrementtkd {
  display: flex;
  justify-content: space-between;
  position: relative;
  height: 35px;
  color: var(--text-color);
}
.passwrapper .decrementincrementtkd input {
  background: none;
  transition: all 250ms ease-in-out;
  width: 60%;
  padding: 0 12px;
  outline: none;
}
.passwrapper .input-number {
  width: 80px;
  padding: 0 12px;
  vertical-align: top;
  text-align: center;
  outline: none;
}
.passwrapper .input-number,
.passwrapper .input-number-decrement,
.passwrapper .input-number-increment {
  user-select: none;
  font-size: 20px;
  font-family: Julius Sans One;
  text-shadow: 0 4px 4px #282828;
  color: var(--text-color);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgb(0 0 0 / 15%);
  border: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
}
.passwrapper .input-number-decrement,
.passwrapper .input-number-increment {
  width: 21%;
  cursor: pointer;
  font-size: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.passwrapper .input-number-decrement {
  border-right: none;
  border-radius: 30px 0 0 30px;
}
.passwrapper .input-number-increment {
  border-left: none;
  border-radius: 0 30px 30px 0;
}
.passwrapper .range {
  height: 55px;
  padding: 0 49px 0 24px;
  position: relative;
}
.passwrapper .field {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  position: relative;
}
.passwrapper .field .value {
  position: absolute;
  font-size: 18px;
  font-weight: 600;
}
.passwrapper .field .value.left {
  left: -20px;
}
.passwrapper .field .value.right {
  right: -46px;
}
.passwrapper .range input {
  outline: none;
  z-index: 20;
  appearance: none;
  height: 10px;
  transition: opacity 0.2s;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgb(0 0 0 / 15%);
  backdrop-filter: blur(5px);
}
.passwrapper .range input::-webkit-slider-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  background: red;
  border-radius: 50%;
  background: #fe6100;
  cursor: pointer;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 1), 0 0 20px 10px rgb(0 0 0 / 15%),
    inset 0 0 4px 2px rgb(0 0 0 / 15%);
  box-sizing: border-box;
  transition: transform 0.1s;
  cursor: grab;
  border: 3px solid #fe6100;
}
.passwrapper .display1 {
  font-size: 20px !important;
  height: 50px;
  text-align: center;
  line-height: 65px;
  font-family: monospace;
  border: none;
  color: var(--text-color);
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgb(0 0 0 / 15%);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  padding-right: 45px;
}
.passwrapper .button-row {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.passwrapper .range-row {
  width: 100%;
  padding: 0 16px;
}
.passwrapper .w3-row {
  margin-top: 15px;
  position: relative;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: space-around;
}
.passwrapper .w3-row1 {
  position: relative;
  display: flex;
  padding: 0 16px;
  height: 60px;
  width: 100%;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
}
.passwrapper .button {
  -webkit-user-select: none;
  user-select: none;
  outline: none;
  height: 35px;
  border: none;
  background: var(--background-color);
  border-radius: 20px;
  box-shadow: inset 5px 5px 15px #96969680, inset -5px -5px 15px #00000080,
    3px 3px 5px #000000b3;
  transition: box-shadow 0.15s;
  line-height: 0;
  color: var(--text-color);
  padding: 13px;
}
.passwrapper .button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.passwrapper .button > * {
  transition: transform 0.2s ease;
}
.passwrapper .button:hover > * {
  transform: scale(0.975);
}
.passwrapper .button:active > * {
  transform: scale(0.95);
}
.passwrapper .button:active {
  box-shadow: inset 1px 1px 1px #96969680, inset -1px -1px 1px #00000080,
    1px 1px 1px #000000b3;
}
.passwrapper .button:focus {
  outline: none;
}
.passwrapper .qr-code {
  width: 110px;
}
.passwrapper .qrcode {
  display: flex;
  align-items: center;
  justify-content: center;
}
.passwrapper .buttonrow {
  display: flex;
  gap: 10px;
  font-size: 14px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.bodydark .passwrapper .button:active {
  transform: scale(0.9);
  box-shadow: none;
}
.bodydark .passwrapper .button:focus {
  animation: none;
}
@keyframes bulge {
  50% {
    transform: rotate(4deg);
    font-size: 1.2em;
    font-weight: bold;
  }
  100% {
    transform: rotate(0);
    font-size: 0.7em;
    font-weight: bold;
  }
}
@media (min-width: 640px) {
  .passwrapper .buttonrow {
    flex-wrap: wrap;
    flex-direction: row;
  }
  @keyframes bulge {
    50% {
      transform: rotate(4deg);
      font-size: 1.5em;
      font-weight: bold;
    }
    100% {
      transform: rotate(0);
      font-size: 1em;
      font-weight: bold;
    }
  }
}
#copybutton {
  position: absolute;
  top: -1px;
  right: -1px;
  width: auto;
  height: 100%;
  padding: 0 12px;
  border-radius: 0;
  box-shadow: none;
}
